import NextAuth from "next-auth";
import GoogleProvider from "next-auth/providers/google";

export const authOptions = {
  providers: [
    GoogleProvider({
      clientId: process.env.GOOGLE_CLIENT_ID!,
      clientSecret: process.env.GOOGLE_CLIENT_SECRET!,
    }),
  ],
  pages: {
    signIn: "/auth/signin", // Si deseas una p치gina personalizada de login
    error: "/auth/error", // P치gina personalizada para errores
  },
  callbacks: {
    async jwt({ token, account }) {
      if (account?.access_token) {
        token.accessToken = account.access_token; // Almacena el token de acceso
      }
      return token;
    },
    async session({ session, token }) {
      session.accessToken = token.accessToken; // A침ade el token a la sesi칩n
      return session;
    },
  },
  secret: process.env.NEXTAUTH_SECRET, // Un secreto para proteger las sesiones
};

export default NextAuth(authOptions);
